import 'izitoast/dist/css/iziToast.min.css';
import iziToast from 'izitoast';

// const API_BASE_URL = 'https://furniture-store-v2.b.goit.study';
const API_BASE_URL = 'https://furniture-store-v2.b.goit.study/api';

const orderModal = document.querySelector('.order-modal');
const closeButton = document.querySelector('.form-container .close-button');
const orderForm = document.getElementById('orderForm');

let currentFurnitureId = null;
let currentMarkerValue = null;

export function openOrderModal(furnitureId, markerValue) {
    currentFurnitureId = furnitureId;
    currentMarkerValue = markerValue;

    console.log(currentFurnitureId, currentMarkerValue);
    

    orderModal.classList.add('is-open'); 
    document.body.style.overflow = 'hidden'; 
    document.addEventListener('keydown', onEscKeyPress);
}

function closeOrderModal() {
    orderModal.classList.remove('is-open');
    document.body.style.overflow = '';
    document.removeEventListener('keydown', onEscKeyPress);
    orderForm.reset();
}

function onEscKeyPress(event) {
    if (event.key === 'Escape') {
        closeOrderModal();
    }
}

closeButton.addEventListener('click', closeOrderModal);

orderModal.addEventListener('click', (event) => {
    if (event.target === orderModal) {
        closeOrderModal();
    }
});



function validateFormData(data) {
    // –†–µ–≥—É–ª—è—Ä–Ω–∏–π –≤–∏—Ä–∞–∑ –¥–ª—è —Ç–µ–ª–µ—Ñ–æ–Ω—É (–¥–æ–ø—É—Å–∫–∞—î +380XXXXXXXXX, –∞–±–æ 380XXXXXXXXX)
    const phoneRegex = /^\+?380\d{9}$/; 

    if (!data.name || data.name.length < 2) {
        iziToast.error({ message: '–Ü–º\'—è –ø–æ–≤–∏–Ω–Ω–æ –º—ñ—Å—Ç–∏—Ç–∏ –º—ñ–Ω—ñ–º—É–º 2 —Å–∏–º–≤–æ–ª–∏.', position: 'topRight' });
        return false;
    }
    
    // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —á–∏ —Ç–µ–ª–µ—Ñ–æ–Ω –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—î —Ñ–æ—Ä–º–∞—Ç—É –ø—ñ—Å–ª—è –≤–∏–¥–∞–ª–µ–Ω–Ω—è –ø—Ä–æ–±—ñ–ª—ñ–≤/–¥—É–∂–æ–∫
    if (!data.phone || !phoneRegex.test(data.phone.replace(/[\s\(\)\-]/g, ''))) {
        iziToast.error({ message: '–í–≤–µ–¥—ñ—Ç—å –∫–æ—Ä–µ–∫—Ç–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É —É —Ñ–æ—Ä–º–∞—Ç—ñ +380XXXXXXXXX.', position: 'topRight' });
        return false;
    }

    return true;
}

orderForm.addEventListener('submit', async (event) => {
    event.preventDefault();

    const formData = new FormData(orderForm);
    const orderData = Object.fromEntries(formData.entries());

    orderData.furnitureId = currentFurnitureId;
    orderData.marker = currentMarkerValue;

    orderData.modelId = currentFurnitureId;
    orderData.color = currentMarkerValue;
    delete orderData.furnitureId;
    delete orderData.marker;

    if (!validateFormData(orderData)) {
        return;
    }
    
    const submitBtn = orderForm.querySelector('.submit-btn');
    submitBtn.disabled = true;
    submitBtn.textContent = '–ù–∞–¥—Å–∏–ª–∞–Ω–Ω—è...';

    try {
        const response = await fetch(`${API_BASE_URL}/orders`, { 
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(orderData),
        });

        if (!response.ok) {
            const error = await response.json();
            iziToast.error({
                title: '–ü–æ–º–∏–ª–∫–∞!',
                message: error.message || '–ù–µ –≤–¥–∞–ª–æ—Å—è –Ω–∞–¥—ñ—Å–ª–∞—Ç–∏ –∑–∞—è–≤–∫—É. –°–ø—Ä–æ–±—É–π—Ç–µ –ø—ñ–∑–Ω—ñ—à–µ.',
                position: 'topRight'
            });
            return;
        }

        iziToast.success({
            title: '–£—Å–ø—ñ—Ö!',
            message: '–í–∞—à–∞ –∑–∞—è–≤–∫–∞ —É—Å–ø—ñ—à–Ω–æ –Ω–∞–¥—ñ—Å–ª–∞–Ω–∞. –ú–∏ —Å–∫–æ—Ä–æ –∑–≤\'—è–∂–µ–º–æ—Å—è –∑ –í–∞–º–∏.',
            position: 'topRight'
        });
        
        closeOrderModal();

    } catch (error) {
        iziToast.error({
            title: '–ü–æ–º–∏–ª–∫–∞ –º–µ—Ä–µ–∂—ñ!',
            message: '–ü–µ—Ä–µ–≤—ñ—Ä—Ç–µ –≤–∞—à–µ —ñ–Ω—Ç–µ—Ä–Ω–µ—Ç-–∑\'—î–¥–Ω–∞–Ω–Ω—è.',
            position: 'topRight'
        });
    } finally {
        submitBtn.disabled = false;
        submitBtn.textContent = '–ù–∞–¥—ñ—Å–ª–∞—Ç–∏ –∑–∞—è–≤–∫—É';
    }
});